// popup-city

.popup-city {
    &__content {
        border-radius: 2px;
        padding: 80px;
        background: #fff;
        max-width: 760px;
    }

    
    &__title {
        font-weight: 700;
        font-size: 48px;
        line-height: 110%;
        color: var(--temnyy);
        margin-bottom: 32px;
    }

    &__form {
        margin-bottom: 40px;
    }

    &__text {
        padding-bottom: 16px;
        border-bottom: 1px solid rgba(60, 50, 50, 0.1);
        margin-bottom: 16px;
        font-weight: 500;
        font-size: 20px;
        line-height: 130%;
        color: var(--temnyy);
    }

    &__list {
        display: flex;
        gap: 16px 40px;
        flex-wrap: wrap;
        max-height: 190px;
        overflow-y: auto;
        &::-webkit-scrollbar {
            width: 4px;
            background-color: var(--izobrazhenie);
            padding: 5px;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 10px;
            background: var(--akcent);
        }
        & li {
            flex: 1 0 40%;
        }
        & li a {
            text-decoration: underline;
            text-decoration-skip-ink: none;
            color: var(--temnyy);
            position: relative;
            padding-left: 27px;
            transition: all 0.3s ease;
            &:hover {
                color: var(--akcent);
            }
            &::before {
                content: "";
                display: block;
                width: 15px;
                height: 20px;
                background: url("./../img/icons/location.svg") no-repeat center / contain;
                position: absolute;
                top: 0;
                left: 0;
            }
            &.active::before {
                background: url("./../img/icons/location-active.svg") no-repeat center / contain;
            }
        }

    }

    &__close {
        background: rgba(66, 133, 244, 0.1);
        &:hover {
            background: rgba(66, 133, 244, 1);
            & svg path {
                fill: #fff;
            }
        }
    }
}



// media popup-city
@media (max-width: 768px) {
    .popup-city {
        &__content {
            padding: 60px 20px;
        }
    
        &__title {
            font-size: 26px;
            margin-bottom: 20px;
        }

        &__text {
            padding-bottom: 12px;
            margin-bottom: 12px;
            font-size: 18px;;
        }
    
        &__list {
            flex-wrap: nowrap;
            flex-direction: column;
            gap: 14px;
            max-height: 290px;
        }
    }
}

