
// form
.form {
    padding: 120px 0;
    background: url("./../img/form/form-bg.jpg") no-repeat center / cover;
    color: #fff;

    &__title {
        font-weight: 700;
        font-size: 48px;
        line-height: 110%;
        color: #fff;
        margin-bottom: 20px;
    }

    &__text {
        margin-bottom: 40px;
    }

    &__inner {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    &__input {
        margin-bottom: 20px;
    }

    &__call {
        padding-bottom: 20px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        margin-bottom: 20px;

        &_wrap {
            border-radius: 2px;
            background: #fff;
            display: flex;
            justify-content: space-between;
            align-items: end;
            gap: 10px;
            position: relative;
            overflow: hidden;
            padding-top: 209px;
        }

        &_icon {
            position: absolute;
            top: -6px;
            left: 46px;
        }

        &_photo {
            position: absolute;
            bottom: 0;
            right: -20px;

        }

        &_plashka {
            border-radius: 2px;
            padding: 24px;
            box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.08);
            background: #fff;
            max-width: 300px;
            position: relative;
            z-index: 2;
            & p {
                color: var(--temnyy);
                margin-bottom: 12px;
            }
            & a {
                display: flex;
                align-items: center;
                gap: 12px;
                font-weight: 700;
                font-size: 24px;
                line-height: 110%;
                color: var(--temnyy);
                transition: all 0.2s ease-in;
                &:hover {
                    color: var(--akcent);
                }
            }
        }
    }

    &__networks {
        border: 1px solid #fff;
        border-radius: 2px;
        padding: 28px;
        box-shadow: 0 -4px 20px 0 rgba(0, 0, 0, 0.08);
        & p {
            margin-bottom: 16px;
        }
        &_row {
            display: flex;
            gap: 10px;
        }
    }

    &__network {
        display: flex;
        width: 50px;
        height: 50px;
        align-items: center;
        justify-content: center;
        border-radius: 2px;
        background: #fff;
        border: 1px solid #fff;
        transition: all 0.2s ease-in;
        & svg path {
            transition: all 0.2s ease-in;
        }
        
        &:hover {
            background: transparent;
            & svg path {
                fill: #fff;
            }
        }
    }
}

// media form
@media (max-width: 768px) {
    .form {
        padding: 80px 0;
    
        &__title {
            font-size: 26px;
            margin-bottom: 16px;
        }
        &__text {
            margin-bottom: 30px;
        }
        &__input {
            margin-bottom: 10px;
        }
        &__networks {
            padding: 20px;
            & p {
                margin-bottom: 12px;
            }
        }
    }
}
@media (max-width: 600px) {
    .form {
        &__call {
            &_wrap {
                padding-top: 185px;
            }
            &_icon {
                display: none;
            }
            &_photo {
                display: block;
                position: absolute;
                bottom: auto;
                right: auto;
                top: 0;
                left: 50%;
                transform: translate(-50%, 0%);
                width: 100%;
                max-width: 238px;
                
                & img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
    
            }
    
            &_plashka {
                padding: 16px;
                max-width: 100%;
            }
        }
    }
}