
// licenses-page
.licenses-page {
    &__card {
        display: block;
        border-radius: 2px;
        padding: 10px;
        background: rgba(66, 133, 244, 0.1);
        border: 1px solid transparent;
        transition: all 0.2s ease-in;
        
        &:hover {
            border: 1px solid var(--osnovnoy);
        }
    }

    &__img {
        display: block;
        width: 100%;
        margin-bottom: 8px; 
    }

    &__picture {
        display: block;
        width: 100%;
        & img {
            width: 100%;
            height: 100%;
            max-height: 380px;
            object-fit: cover;
            object-position: top;
            border-radius: 2px;
        }
    }

    &__label {
        border-radius: 2px;
        padding: 16px 20px;
        background: #fff;
        text-align: center;
    }
}

@media (max-width: 768px) {
    .licenses-page {
        &__label {
            padding: 12px 16px;
        }
    }
}