
// living
.living {
    overflow: hidden;
    &__swiper {
        display: contents;
        & .swiper-wrapper {
            display: contents;
        }
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;
        margin-bottom: 24px;

        & a {
            display: block;
        }
        & picture {
            display: block;
            width: 100%;
            height: 100%;
            & img {
                display: block;
                width: 100%;
                height: 100%;
                max-height: 200px;
                object-fit: cover;
                border-radius: 2px;
                float: none !important;
                margin: 0 !important;
            }
        }
    }

    &__gallerys {
        margin-top: 60px;
    }
    &__gallery {
        &:not(:last-child) {
            padding-bottom: 32px;
            border-bottom: 1px solid rgba(60, 50, 50, 0.1);
            margin-bottom: 32px;
        }

        &_title {
            font-weight: 700;
            font-size: 36px;
            line-height: 110%;
            color: var(--temnyy);
            margin-bottom: 32px;
        }
    }
}

.gallery {
    &__swiper-paginations {
        margin-top: 24px;
    }
    &__picture {
        & img {
            max-width: 100%;
            margin: 0;
            float: none;
        }
    }
}

// media living
@media (max-width: 1100px) {
    .living {
        &__cards {
            grid-template-columns: repeat(3, 1fr);
            gap: 20px;
        }
    }
}
@media (max-width: 768px) {
    .living {
        overflow: hidden;
        &__swiper {
            display: block;
            margin-bottom: 14px;
            overflow: visible;
            & .swiper-wrapper {
                display: flex;
            }
        }
    
        &__cards {
            display: block;
            margin-bottom: 20px;
    
            & a {
                display: block;
            }
            & picture {
                display: block;
                width: 100%;
                height: 100%;
                & img {
                    display: block;
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    border-radius: 2px;
                }
            }
        }

        &__gallerys {
            padding-top: 20px;
            border-top: 1px solid rgba(60, 50, 50, 0.1);
            margin-top: 20px;
        }
        &__gallery {
            &:not(:last-child) {
                padding-bottom: 20px;
                margin-bottom: 20px;
            }
    
            &_title {
                font-size: 24px;
                margin-bottom: 16px;
            }
        }
    }

    .gallery {
        &__swiper {
            overflow: visible;
        }
        &__swiper-paginations {
            margin-top: 14px;
            & button {
                display: none;
            }
        }
        &__picture {
            & img {
                margin: 0;
            }
        }
    }
}