
// intro 
.intro {
    padding: 100px 0 160px;
    background: url("./../img/intro/intro-bg.jpg") no-repeat center / cover;

    &__title {
        text-align: center;
        margin: 0 auto;
        margin-bottom: 28px;
    }

    &__items {
        margin-bottom: 44px;
        & ul {
            color: #fff;
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 20px;
            & li {
                padding-left: 34px;
                position: relative;
                &:not(:last-child) {
                    padding-right: 20px;
                    border-right: 1px solid rgba(255, 255, 255, 0.1);
                }
                &::before {
                    content: "";
                    display: block;
                    width: 24px;
                    height: 24px;
                    background: url("./../img/icons/like.svg") no-repeat center / contain;
                    position: absolute;
                    top: -4px;
                    left: 0;
                }
            }
        }
    }

    &__btn {
        display: block;
        margin: 0 auto;
        width: 100%;
        max-width: 300px;
    }
}

// media intro 
@media (max-width: 1024px) {
    .intro {
        padding: 40px 0 160px;

        &__title {
            margin-bottom: 24px;
        }

        &__items {
            margin-bottom: 24px;
            & ul {
                flex-direction: column;
                align-items: stretch;
                gap: 10px;
                & li {
                    padding-left: 28px;
                    &:not(:last-child) {
                        padding-right: 0;
                        border-right: none;
                        padding-bottom: 10px;
                        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
                    }
                    &::before {
                        width: 18px;
                        height: 18px;
                        top: 0px;
                    }
                }
            }
        }
    }
}
@media (max-width: 440px) {
    .intro {
        &__btn {
            max-width: 100%;
        }
    }
}

// services 
.services {
    margin-top: -100px;

    &__wrap {
        border-radius: 2px;
        padding: 40px;
        box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.08);
        background: #fff;
    }

    &__items {
        display: flex;
        gap: 22px;
        margin-bottom: 32px;
    }

    &__item {
        flex-grow: 1;
        &_top {
            margin-bottom: 20px;
        }
    }

    &__item:not(:last-child) {
        padding-right: 22px;
        border-right: 1px solid rgba(60, 50, 50, 0.1);
    }

    &__title {
        display: block;
        font-weight: 700;
        font-size: 24px;
        line-height: 110%;
        color: var(--temnyy);
        margin-bottom: 12px;
        transition: all 0.3s ease;
        &:hover {
            color: var(--osnovnoy);
        }
    }

    &__price {
        font-weight: 500;
        color: var(--osnovnoy);
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 14px;

        & a {
            display: block;
            text-decoration: underline;
            text-decoration-skip-ink: none;
            color: var(--temnyy);
            position: relative;
            padding-left: 20px;
            transition: all 0.3s ease;
            &:hover {
                color: var(--akcent);
            }
            &::before {
                content: "";
                display: block;
                width: 8px;
                height: 8px;
                border-radius: 100%;
                background: var(--akcent);
                position: absolute;
                top: 6px;
                left: 0;
            }
        }
    }

    &__btn {
        border-radius: 2px;
        padding: 25px 32px;
        background: rgba(237, 69, 69, 0.05);
        color: var(--akcent);
        display: flex;
        align-items: center;
        justify-content: space-between;
        border: 1px solid transparent;
        transition: all 0.3s ease;

        &:hover {
            border: 1px solid var(--akcent);
        }
    }
}

// media services 
@media (max-width: 1024px) {
    .services {
        &__items {
            flex-wrap: wrap;
        }

        &__item {
            flex: 1 0 40%;
        }

        &__item:nth-child(2n) {
            padding-right: 0;
            border-right: none;
        }
    }
}
@media (max-width: 768px) {
    .services {
        margin-top: -120px;

        &__wrap {
            padding: 20px;
        }

        &__items {
            flex-direction: column;
            gap: 20px;
            margin-bottom: 20px;
        }

        &__item {
            flex-grow: 1;
        }

        &__item:nth-child(n) {
            padding-right: 0;
            border-right: none;
        }

        &__item {
            padding-bottom: 22px;
            border-bottom: 1px solid rgba(60, 50, 50, 0.1);
            &_top {
                display: flex;
                justify-content: space-between;
                align-items: center;
                gap: 10px;
                margin-bottom: 17px;
            }
        }

        &__title {
            font-size: 20px;
            margin-bottom: 0;
        }

        &__list {
            gap: 10px;

            & a {
                padding-left: 16px;
                &::before {
                    width: 6px;
                    height: 6px;
                    top: 6px;
                    left: 0;
                }
            }
        }

        &__btn {
            padding: 16px 20px;
        }
    }
}



// intro-service 
.intro-service {
    & .breadcrumbs {
        margin-bottom: 20px;
    }

    &__wrap {
        padding: 60px;
        background: url("./../img/intro/intro-service-bg.jpg") no-repeat center / cover;
        position: relative;
        overflow: hidden;
        &::after {
            content: "";
            display: block;
            width: 1998px;
            aspect-ratio: 1 / 1;
            border-radius: 100%;
            background: rgba(66, 133, 244, 0.7);
            position: absolute;
            bottom: -292px;
            right: 117px;
        }
        &::before {
            content: "";
            display: block;
            width: 1776px;
            aspect-ratio: 1 / 1;
            border-radius: 100%;
            background: rgba(66, 133, 244, 0.7);
            position: absolute;
            bottom: -181px;
            right: 228px;
        }
    }

    &__title {
        margin-bottom: 28px;
        max-width: 80%;
        text-align: left;
        position: relative;
        z-index: 2;
    }

    &__items {
        margin-bottom: 87px;
        position: relative;
        z-index: 2;
        & ul {
            color: #fff;
            width: fit-content;
            & li {
                padding-left: 34px;
                position: relative;
                &:not(:last-child) {
                    padding-bottom: 14px;
                    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
                    margin-bottom: 14px;
                }
                &::before {
                    content: "";
                    display: block;
                    width: 24px;
                    height: 24px;
                    background: url("./../img/icons/like.svg") no-repeat center / contain;
                    position: absolute;
                    top: -4px;
                    left: 0;
                }
            }
        }
    }

    &__btn {
        display: block;
        width: 100%;
        max-width: 300px;
    }

    &__price {
        position: absolute;
        bottom: 0;
        right: 0;
        text-align: right;
        color: rgba(255, 255, 255, 0.5);
        padding: 37px 24px 24px;
        width: 343px;
        height: 132px;
        background: #ED4545;
        -webkit-mask-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="343" height="132" viewBox="0 0 343 132"><path d="M617 132C617 96.9914 584.497 63.4167 526.642 38.6619C468.787 13.9071 390.319 0 308.5 0C226.681 0 148.213 13.9071 90.3576 38.6619C32.5026 63.4167 0 96.9914 0 132H617Z" fill="black"/></svg>');
        -webkit-mask-repeat: no-repeat;
        -webkit-mask-size: contain;
        mask-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="343" height="132" viewBox="0 0 343 132"><path d="M617 132C617 96.9914 584.497 63.4167 526.642 38.6619C468.787 13.9071 390.319 0 308.5 0C226.681 0 148.213 13.9071 90.3576 38.6619C32.5026 63.4167 0 96.9914 0 132H617Z" fill="black"/></svg>');
        mask-repeat: no-repeat;
        mask-size: contain;

        & span {
            display: block;
            font-weight: 700;
            font-size: 36px;
            line-height: 110%;
            color: #fff;
            margin-top: 8px;
        }
    }
}

// media intro 
