
// about
.about {
    &__text > p {
        margin-bottom: 24px;
    }
    &__items {
        border: 1px solid var(--osnovnoy);
        border-radius: 2px;
        padding: 28px;
        & li {
            &:not(:last-child) {
                padding-bottom: 20px;
                border-bottom: 1px solid rgba(60, 50, 50, 0.1);
                margin-bottom: 20px;
            }

            & span {
                display: block;
                font-weight: 700;
                font-size: 24px;
                line-height: 110%;
                color: var(--osnovnoy);
                margin-bottom: 8px;
            }
        }
    }

    &__swiper-slide {
        display: block;
        width: 100%;
        height: 100%;

        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 2px;
        }
    }

    &__swiper-paginations {
        border-radius: 2px;
        padding: 10px;
        background: #fff;
        position: absolute;
        bottom: 10px;
        left: 10px;
        right: 10px;
        z-index: 2;
        justify-content: space-between;
    }

}

// media about 
@media (max-width: 1100px) {
    .about {
        &__swiper-slide {
            & img {
                max-height: 500px;
            }
        }
    }
}
@media (max-width: 768px) {
    .about {
        overflow: hidden;
        &__row {
            flex-direction: column-reverse;
        }

        &__text > p {
            margin-bottom: 20px;
        }
        &__items {
            padding: 20px;
            & li {
                &:not(:last-child) {
                    padding-bottom: 12px;
                    margin-bottom: 12px;
                }
            }
        }

        &__swiper {
            overflow: visible;
        }

        &__swiper-slide {
            & img {
                max-height: 350px;
            }
        }

        &__swiper-paginations {
            border-radius: 0;
            padding: 0;
            background: transparent;
            position: static;
            justify-content: center;
            margin-top: 14px;
        }

        &__swiper-button-prev, &__swiper-button-next {
            display: none;
        }

    }
}