// popup-search-2

.popup-search {
    &__body {
        display: flex;
        justify-content: flex-end;
        align-items: stretch;
        padding: 0;
    }
    &__content {
        padding: 70px 0;
        box-shadow: 0 4px 40px 0 rgba(0, 0, 0, 0.06);
        background: #fff;
        max-width: 100%;
        height: max-content;
        max-height: 100vh;

        -webkit-transform: translateY(-100%);
        transform: translateY(-100%);
    }

    &__form {
        width: calc(100% - 94px);
        margin-bottom: 40px;
    }

    &__search, &__search-result {
        width: calc(100% - 94px);
    }

    &__list_result {
        max-height: 600px;
        overflow-y: auto;
        &::-webkit-scrollbar {
            width: 4px;
            background-color: var(--izobrazhenie);
            padding: 5px;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 10px;
            background: var(--akcent);
        }

        & li {
            &:not(:last-child) {
                padding-bottom: 12px;
                border-bottom: 1px solid rgba(60, 50, 50, 0.1);
                margin-bottom: 12px;
            }
            & a {
                display: block;
                &:hover p {
                    color: var(--akcent);
                }
                & p {
                    font-weight: 500;
                    font-size: 20px;
                    color: var(--temnyy);
                    margin-bottom: 8px;
                }
                & span {
                    font-size: 16px;
                    color: var(--akcent);
                }
            }
        }
    }

    &__close {
        width: 62px;
        height: 62px;
        top: 70px;
        right: 40px;
        background: rgba(66, 133, 244, 0.1);
        &:hover {
            background: rgba(66, 133, 244, 1);
            & svg path {
                fill: #fff;
            }
        }
    }
}

.popup.open .popup-search__content {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

// media popup-search
@media (max-width: 750px) {
    .popup-search {
        &__form {
            width: 100%;
            margin-bottom: 0px;
            & input {
                width: 100%;
            }
        }
    
        &__search, &__search-result {
            width: 100%;
        }
    
        &__list_result {
            position: absolute;
            top: 61px;
            left: 0;
            right: 0;
            z-index: 3;
            padding: 30px 10px;
            background: #fff;
            min-height: 545px;
            max-height: 545px;
            box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.08);
    
            & li {
                & a {
                    & p {
                        font-size: 16px;
                        margin-bottom: 6px;
                    }
                }
            }
        }
    }
}
