
// doctors
.doctors {
    overflow: hidden;
    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 32px;
        margin-bottom: 32px;
        position: relative;
        &::after {
            content: "";
            display: inline-block;
            width: calc(100% - 188px - 198px);
            height: 2px;
            background: rgba(66, 133, 244, 0.2);
            position: absolute;
            top: 50%;
            left: 188px;
        }
    }

    &__title {
        font-weight: 700;
        font-size: 24px;
        line-height: 110%;
        color: var(--temnyy);
    }

    &__btn {
        padding: 16px 40px;
    }


    &__swiper {
        height: auto;
        margin-bottom: 24px;
    }


    &__card {
        border-radius: 2px;
        padding: 20px;
        background: #fff;

        &_content {
            display: flex;
            align-items: center;
            gap: 20px;
            margin-bottom: 20px;
        }

        &_photo {
            display: block;
            width: 100%;
            max-width: 220px;
            border: 1px solid rgba(66, 133, 244, 0.1);
            background: rgba(66, 133, 244, 0.05);
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                max-width: 220px;
                max-height: 220px;
                aspect-ratio: 1 / 1;
                border-radius: 2px;
            }
        }

        &_fio {
            font-weight: 700;
            font-size: 24px;
            line-height: 110%;
            color: var(--temnyy);
            margin-bottom: 24px;
        }

        &_info {
            display: flex;
            flex-direction: column;
            gap: 12px;
            & li {
                position: relative;
                padding-left: 32px;
                &::before {
                    content: "";
                    display: block;
                    width: 24px;
                    height: 24px;
                    position: absolute;
                    top: 0;
                    left: 0;
                    background: url("./../img/icons/doctor-red.svg") no-repeat center / contain;
                }
                &:first-child::before {
                    background: url("./../img/icons/doctor-red.svg") no-repeat center / contain;
                }
                &:last-child::before {
                    background: url("./../img/icons/bag-red.svg") no-repeat center / contain;
                }
            }
        }

        &_btns {
            display: flex;
            gap: 8px;
            width: 100%;
            & > * {
                flex-grow: 1;
            }
        }
    }
}

// licenses
.licenses {
    &__top {
        &::after {
            width: calc(100% - 144px - 229px);
            left: 144px;
        }
    }

    &__swiper {
        height: auto;
        margin-bottom: 24px;
    }

    &__picture {
        display: block;
        border-radius: 2px;
        padding: 10px;
        background: #fff;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: top;
            max-height: 323px;
            border-radius: 2px;
        }
    }
}


.doctors-page {
    &__tab-btns {
        flex-wrap: wrap;
        padding-bottom: 40px;
        border-bottom: 1px solid rgba(60, 50, 50, 0.1);
        margin-bottom: 40px;
    }

    &__card {
        border-radius: 2px;
        padding: 28px;
        box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
        background: #fff;

        &_photo {
            display: block;
            width: 100%;
            border: 1px solid rgba(66, 133, 244, 0.1);
            background: rgba(66, 133, 244, 0.05);
            border-radius: 2px;
            margin-bottom: 20px;
            & img {
                display: block;
                width: 100%;
                height: 100%;
                max-width: 260px;
                max-height: 220px;
                object-fit: cover;
                object-position: top;
                margin: 0 auto;
                border-radius: 2px;
            }
        }

        &_fio {
            font-weight: 700;
            font-size: 24px;
            line-height: 110%;
            color: var(--temnyy);
            margin-bottom: 24px;
        }

        &_info {
            display: flex;
            flex-direction: column;
            gap: 12px;
            margin-bottom: 46px;
            & li {
                position: relative;
                padding-left: 32px;
                &::before {
                    content: "";
                    display: block;
                    width: 24px;
                    height: 24px;
                    position: absolute;
                    top: 0;
                    left: 0;
                    background: url("./../img/icons/doctor-red.svg") no-repeat center / contain;
                }
                &:first-child::before {
                    background: url("./../img/icons/doctor-red.svg") no-repeat center / contain;
                }
                &:last-child::before {
                    background: url("./../img/icons/bag-red.svg") no-repeat center / contain;
                }
            }
        }

        &_btns {
            display: flex;
            flex-direction: column;
            gap: 8px;
            width: 100%;
        }
    }
}

// media doctors
@media (max-width: 1100px) {
    .doctors {
        &__row {
            gap: 40px;
        }
        &__swiper-button-prev, &__swiper-button-next {
            display: none;
        }
        &__swiper {
            overflow: visible;
        }
    }
    .licenses {
        &__swiper-button-prev, &__swiper-button-next {
            display: none;
        }
        &__swiper {
            overflow: visible;
        }
    }
}

@media (max-width: 768px) {
    .doctors {
        &__row {
            gap: 30px;
        }
        &__block {
            display: flex;
            flex-direction: column;
            gap: 20px;
        }
        &__top {
            display: contents;
            &::after {
                content: none;
            }
        }
        &__title {
            font-size: 20px;
            display: flex;
            align-items: center;
            gap: 32px;
            &::after {
                content: "";
                display: inline-block;
                width: 100%;
                height: 2px;
                background: rgba(66, 133, 244, 0.2);
            }
        }
        &__btn {
            order: 3;
        }
        &__swiper {
            margin-bottom: 0;
        }
        &__swiper-button-prev, &__swiper-button-next {
            display: none;
        }
    }
    
    .licenses {
        &__swiper {
            margin-bottom: 0;
        }
        &__picture {
            & img {
                max-height: 380px;
            }
        }
    }

    .doctors-page {
        &__tab-btns {
            padding-bottom: 30px;
            margin-bottom: 30px;
        }
    
        &__card {
            padding: 20px;
            &_fio {
                font-size: 20px;
                margin-bottom: 14px;
            }
    
            &_info {
                gap: 10px;
                margin-bottom: 20px;
                & li {
                    position: relative;
                    padding-left: 26px;
                    &::before {
                        width: 18px;
                        height: 18px;
                    }
                }
            }
        }
    }
}

@media (max-width: 560px) {
    .doctors {
        &__card {
            padding: 20px;
    
            &_content {
                flex-direction: column;
                align-items: stretch;
            }
    
            &_photo {
                max-width: 100%;
                & img {
                    margin: 0 auto;
                    max-width: 320px;
                    max-height: 250px;
                    object-position: top;
                }
            }
    
            &_fio {
                font-size: 20px;
                margin-bottom: 14px;
            }
    
            &_info {
                gap: 10px;
                & li {
                    padding-left: 26px;
                    &::before {
                        width: 18px;
                        height: 18px;
                    }
                }
            }
    
            &_btns {
                flex-direction: column;
            }
        }
    }
}