
// services-page
.services-page {
    &__item:not(:last-child) {
        padding-bottom: 32px;
        border-bottom: 1px solid rgba(60, 50, 50, 0.1);
        margin-bottom: 32px;
    }

    &__accordion, &__content {
        display: contents;
    }

    &__title {
        display: block;
        font-weight: 700;
        font-size: 36px;
        line-height: 110%;
        color: var(--temnyy);
        width: fit-content;
        margin-bottom: 24px;
        transition: all 0.2s ease-in;
        &:hover {
            color: var(--akcent);
        }
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        gap: 20px 40px;
        list-style: none;

        & li {
            width: 100%;
            max-width: calc(100% / 4 - 30px);
            position: relative;
            padding-left: 20px;

            &::before {
                content: "";
                display: block;
                width: 8px;
                height: 8px;
                border-radius: 100%;
                background: var(--akcent);
                position: absolute;
                top: 8px;
                left: 0;
            }

            & a {
                text-decoration: underline;
                color: var(--temnyy);
                transition: all 0.2s ease-in;
                &:hover {
                    color: var(--akcent);
                }
            }
        }
    }
}

// media services-page
@media (max-width: 1200px) {
    .services-page {
        &__list li {
            max-width: calc(100% / 3 - 28px);
        }
    }
}
@media (max-width: 1000px) {
    .services-page {
        &__list li {
            max-width: calc(50% - 20px);
        }
    }
}
@media (max-width: 768px) {
    .services-page {
        &__items {
            grid-template-columns: repeat(1, 1fr);
            gap: 16px;
        }
    
        &__accordion, &__content {
            display: block;
        }

        &__item:not(:last-child) {
            padding-bottom: 20px;
            margin-bottom: 20px;
        }

        &__accordion {
            padding-right: 20px;
            position: relative;
            &::after {
                content: "";
                display: block;
                width: 10px;
                height: 10px;
                border-bottom: 2px solid var(--akcent);
                border-right: 2px solid var(--akcent);
                transform: rotate(45deg);
                position: absolute;
                top: 5px;
                right: 0;
            }

            &.accordion--active::after {
                transform: rotate(225deg);
            }
        }
    
        &__title {
            font-size: 20px;
            margin: 0;
        }
    
        &__list {
            flex-direction: column;
            gap: 12px;
            margin-top: 20px;
            & li {
                max-width: 100%;
                &::before {
                    width: 6px;
                    height: 6px;
                }
            }
        }
    }
}