
// appeal
.appeal {
    &__wrap {
        padding: 52px;
        box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.08);
        background: #fff;
        display: flex;
        gap: 40px;
    }

    &__photo {
        display: block;
        width: 100%;
        max-width: 320px;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            max-width: 320px;
            max-height: 400px;
            border-radius: 2px;
        }
    }

    &__fio {
        font-weight: 700;
        font-size: 36px;
        line-height: 110%;
        color: var(--temnyy);
        margin-bottom: 16px;
    }

    &__info {
        display: flex;
        flex-direction: column;
        gap: 11px;
        padding-bottom: 32px;
        border-bottom: 1px solid rgba(60, 50, 50, 0.1);
        margin-bottom: 32px;
        & li {
            display: flex;
            align-items: center;
            gap: 8px;
            & img {
                min-width: 24px;
                width: 24px;
                height: 24px;
                object-fit: contain;
            }
        }
    }

    &__text p:not(:last-child) {
        margin-bottom: 12px;
    }

}

// media appeal
@media (max-width: 1024px) {
    .appeal {
        &__wrap {
            padding: 30px;
            display: flex;
            flex-direction: column;
            gap: 0;
        }
    
        &__photo {
            max-width: 480px;
            margin: 0 auto;
            margin-bottom: 20px;
            & img {
                max-width: 480px;
                max-height: 460px;

            }
        }

        &__content {
            display: contents;
        }
    
        &__info {
            padding-bottom: 0;
            border-bottom: none;
            margin-bottom: 0;
        }
    
        &__text {
            order: -1;
            padding-bottom: 20px;
            border-bottom: 1px solid rgba(60, 50, 50, 0.1);
            margin-bottom: 20px;
        }
    
    }
}
@media (max-width: 768px) {
    .appeal {
        &__wrap {
            padding: 0;
            box-shadow: none;
        }
    
        &__photo {
            margin-bottom: 16px;
            & img {
                max-width: 480px;
                max-height: 380px;
                object-position: top;
            }
        }

        &__fio {
            font-size: 24px;
            margin-bottom: 14px;
        }
    
        &__info {
            gap: 10px;
            & li {
                & img {
                    min-width: 18px;
                    width: 18px;
                    height: 18px;
                }
            }
        }
    }
}