
// why-us 
.why-us {
    overflow: hidden;
    &__list {
        display: flex;
        flex-wrap: wrap;
        gap: 64px 32px;
    }

    &__item {
        flex-grow: 1;
        max-width: calc(100% / 3 - 22px);
        display: flex;
        gap: 20px;
        position: relative;

        padding-right: 32px;
        border-right: 1px solid rgba(66, 133, 244, 0.2);
        &:nth-child(3n) {
            padding-right: 0;
            border-right: none;
        }
        &:nth-last-child(n+4)::after {
            content: "";
            display: block;
            width: calc(100% + 32px);
            height: 1px;
            background: rgba(66, 133, 244, 0.2);
            position: absolute;
            bottom: -32px;
            left: -15px;
        }

        &_icon {
            display: flex;
            align-items: center;
            justify-content: center;
            min-width: 60px;
            width: 60px;
            height: 60px;
            border-radius: 100px;
            background: var(--osnovnoy);
        }

        &_title {
            font-weight: 700;
            font-size: 24px;
            line-height: 110%;
            color: var(--temnyy);
            margin-bottom: 12px;
        }
    }
}

// media why-us 
@media (max-width: 1100px) {
    .why-us {
        &__item {
            max-width: calc(50% - 22px);
            &:nth-child(n) {
                padding-right: 32px;
                border-right: 1px solid rgba(66, 133, 244, 0.2);
            }
            &:nth-child(2n) {
                padding-right: 0;
                border-right: none;
            }
            &:nth-last-child(n+3)::after {
                content: "";
                display: block;
                width: calc(100% + 32px);
                height: 1px;
                background: rgba(66, 133, 244, 0.2);
                position: absolute;
                bottom: -32px;
                left: 0;
            }
        }
    }
}
@media (max-width: 768px) {
    .why-us {
        &__list {
            flex-direction: column;
            gap: 20px;
        }

        &__item {
            max-width: 100%;
            gap: 16px;
            
            &:nth-child(n) {
                padding-right: 0;
                border-right: none;
                padding-bottom: 20px;
                border-bottom: 1px solid rgba(66, 133, 244, 0.2);
            }
            &:last-child {
                padding-bottom: 0;
                border-bottom: none;
            }
            &:nth-child(n)::after {
                content: none;
            }

            &_icon {
                min-width: 44px;
                width: 44px;
                height: 44px;
                padding: 10px;
            }

            &_title {
                font-size: 20px;
            }
        }
    }
}