
// contacts
.contacts {
    &__items {
        display: flex;
        gap: 20px;
        margin-bottom: 60px;
    }

    &__item {
        flex: 1 0 20%;
        &:not(:last-child) {
            padding-right: 20px;
            border-right: 1px solid rgba(60, 50, 50, 0.1);
        }
    }

    &__text {
        font-size: 16px;
        line-height: 130%;
        color: rgba(60, 50, 50, 0.4);
        margin-bottom: 12px;
    }

    &__link {
        font-weight: 700;
        font-size: 24px;
        line-height: 110%;
        color: var(--temnyy);
        text-align: left;
        transition: all 0.2s ease-in;
        &:hover {
            color: var(--akcent);
        }
    }

    &__networks {
        display: flex;
        gap: 8px;
    }

    &__network {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 56px;
        height: 56px;
        border-radius: 2px;
        background: #fff;
        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.08);
        transition: all 0.3s ease;
        & svg path {
            transition: all 0.3s ease;
        }
        &:hover svg path  {
            fill: #fff;
        }
        &.wa:hover  {
            background: #43D854;
        }
        &.ok:hover  {
            background: #ED812B;
        }
        &.tg:hover  {
            background: #27A7E7;
        }
    }

    &__maps {
        border-radius: 2px;
        width: 100%;
        height: 100%;
        min-height: 520px;
        position: relative;
        overflow: hidden;
    }

    &__map {
        position: absolute;
        inset: 0;
        z-index: 2;
        width: 100%;
        height: 100%;
    }
}

// media navigation
@media (max-width: 1024px) {
    .contacts {
        &__items {
            display: flex;
            flex-wrap: wrap;
        }
    
        &__item {
            flex: 1 0 40%;
            &:nth-child(2) {
                padding-right: 0;
                border-right: none;
            }
        }
    }
}
@media (max-width: 768px) {
    .contacts {
        margin-bottom: 0;
        overflow: hidden;
        &__items {
            flex-direction: column;
            gap: 24px;
            margin-bottom: 40px;
        }
        &__item {
            padding-right: 0;
            border-right: none;
        }
        &__link {
            font-size: 20px;
        }
        &__network {
            width: 50px;
            height: 50px;
        }
    
        &__maps {
            width: calc(100% + 40px);
            min-height: 400px;
            margin: 0 -20px;
        }
    }
}