
// methods
.methods {

    &__swiper {
        padding-bottom: 30px;
        border-bottom: 1px solid rgba(60, 50, 50, 0.1);
        margin-bottom: 30px;
    }

    &__swiper-slide {
        border: 1px solid var(--osnovnoy);
        border-radius: 2px;
        padding: 24px;
        height: auto;
    }

    &__name {
        font-weight: 700;
        font-size: 24px;
        line-height: 110%;
        color: var(--osnovnoy);
        margin-bottom: 20px;
    }

    &__text {
        max-height: 155px;
        overflow: hidden;
        margin-bottom: 16px;
    }

    &__paginations {
        margin-top: 24px;
    }


    &__list {
        &_title {
            font-weight: 700;
            font-size: 36px;
            line-height: 110%;
            margin-bottom: 28px;
        }

        & ul {
            columns: 2;
            column-gap: 40px;
            margin-bottom: 20px;

            & li {
                position: relative;
                padding-left: 30px;
                margin-bottom: 20px;

                &::before {
                    content: "";
                    display: block;
                    width: 20px;
                    height: 19px;
                    background: url("./../img/icons/mark-blue.svg") no-repeat center / contain;
                    position: absolute;
                    top: 0;
                    left: 0;
                }
                
                &:nth-child(even)::before {
                    background: url("./../img/icons/mark-red.svg") no-repeat center / contain;
                }
            }
        }

        &_footnote {
            font-size: 16px;
            line-height: 130%;
            color: rgba(60, 50, 50, 0.6);
        }
    }
}

// media methods
@media (max-width: 768px) {
    .methods {
        overflow: hidden;
        &__swiper {
            overflow: visible;
            padding-bottom: 20px;
            margin-bottom: 20px;
        }
    
        &__swiper-slide {
            padding: 20px;
        }
    
        &__name {
            font-size: 20px;
            margin-bottom: 12px;
        }
    
        &__text {
            max-height: 110px;
        }
    
        &__paginations {
            margin-top: 14px;
        }

        &__swiper-button-next, &__swiper-button-prev {
            display: none;
        }
    
    
        &__list {
            &_title {
                font-size: 24px;
                margin-bottom: 20px;
            }
    
            & ul {
                columns: 1;
                column-gap: 40px;
                margin-bottom: 6px;
    
                & li {
                    padding-left: 27px;
                    margin-bottom: 14px;
                    &::before {
                        width: 17px;
                        height: 16px;
                    }
                }
            }
    
            &_footnote {
                font-size: 14px;
            }
        }
    }
    
}