
// calculator
.calculator {
    &__row {
        display: flex;
        gap: 40px;
    }
    
    &__left {
        flex: 0 0 300px;
    }

    &__right {
        flex-grow: 1;
        max-width: calc(100% - 340px);
    }

    &__selects {
        padding-bottom: 24px;
        border-bottom: 1px solid rgba(60, 50, 50, 0.1);
        margin-bottom: 24px;
        display: flex;
        flex-wrap: wrap;
        gap: 44px;
    }

    &__select {
        width: 100%;
        max-width: calc(50% - 22px);
    }

    &__inner {
        border: 1px solid rgba(66, 133, 244, 0.1);
        padding: 40px;
        background: #f6f9fe;

        &_title {
            font-weight: 700;
            font-size: 24px;
            line-height: 110%;
            color: var(--temnyy);
            margin-bottom: 24px;
        }

        &_columns {
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            gap: 20px 40px;
        }
    }

    &__input {
        flex: 1 0 40%;
    }

    &__btn {
        width: 100%;
        max-width: 300px;
    }

    &__politic {
        width: calc(100% - 340px);
    }
}

// media calculator
@media (max-width: 1024px) {
    .calculator {
        &__row {
            flex-direction: column;
            gap: 30px;
        }
        &__left {
            display: contents;
        }
        &__right {
            display: contents;
        }
    }
}
@media (max-width: 768px) {
    .calculator {
        &__selects {
            gap: 24px;
        }
        &__select {
            width: 100%;
            max-width: 100%;
        }
    
        &__inner {
            padding: 20px;
            &_title {
                font-size: 20px;
                margin-bottom: 16px;
            }
    
            &_columns {
                flex-direction: column;
                align-items: stretch;
                gap: 20px;
            }
        }
        &__btn {
            max-width: 100%;
        }
        &__politic {
            width: 100%;
        }
    }
}