
// footer
.footer {
    padding: 120px 0 60px;
    background: var(--temnyy);

    &__block {
        padding-bottom: 40px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        margin-bottom: 40px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
    }

    &__info {
        display: flex;
        gap: 40px;

        &_text {
            font-size: 16px;
            line-height: 130%;
            color: rgba(255, 255, 255, 0.4);
            margin-bottom: 10px;
        }

        &_link {
            font-weight: 500;
            line-height: 130%;
            color: #fff;
            text-align: left;
        }
    }

    &__networks {
        display: flex;
        gap: 8px;
    }

    &__network {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50px;
        height: 50px;
        border-radius: 2px;
        background: #fff;
        border: 1px solid #fff;
        transition: all 0.2s ease-in;
        
        &:hover {
            background: transparent;
        }
    }

    &__nav {
        display: flex;
        gap: 20px;
        width: 100%;
        &_item {
            flex-grow: 1;
            &:not(:last-child) {
                padding-right: 20px;
                border-right: 1px solid rgba(255, 255, 255, 0.1);
            }
        }

        &_acc {
            display: block;
            margin-bottom: 14px;
            pointer-events: none;
            
            & a {
                pointer-events: all;
                font-weight: 700;
                font-size: 20px;
                line-height: 110%;
                color: #fff;
                transition: all 0.2s ease-in;
                    
                &:hover {
                    color: rgba(255, 255, 255, 0.6);
                }
            }
        }

        & ul {
            & a {
                display: block;
                font-size: 16px;
                line-height: 130%;
                text-decoration: underline;
                text-decoration-skip-ink: none;
                color: #fff;
                padding: 7px 0;
                padding-left: 20px;
                position: relative;
                transition: all 0.2s ease-in;
                
                &:hover {
                    color: rgba(255, 255, 255, 0.6);
                }

                &::before {
                    content: "";
                    display: block;
                    width: 8px;
                    height: 8px;
                    border-radius: 100%;
                    background: var(--akcent);
                    position: absolute;
                    top: 15px;
                    left: 0;
                }
            }
        }
    }

    &__copy {
        & p {
            font-size: 14px;
            line-height: 130%;
            color: rgba(255, 255, 255, 0.4);
            margin-bottom: 16px;
        }
        & a {
            font-size: 16px;
            line-height: 130%;
            text-decoration: underline;
            color: #fff;
            &:hover {
                opacity: 0.6;
            }
        }
    }
}

// media footer
@media (max-width: 1200px) {
    .footer {
        &__nav {
            flex-wrap: wrap;
            &_item {
                width: 100%;
                max-width: calc(33% - 12px);
            }
        }
        &__info .footer__info_item {
            flex: 1 0 30%;
        }
    }
}
@media (max-width: 870px) {
    .footer {
        padding: 80px 0 40px;

        &__block {
            padding-bottom: 0;
            border-bottom: none;
            margin-bottom: 0;
        }

        &__top {
            display: contents;
        }

        &__inner {
            display: flex;
            flex-direction: column;
            gap: 30px;
            margin-bottom: 40px;
        }

        &__btn {
            order: 3;
        }
    
    
        &__info {
            flex-direction: column;
            gap: 22px;
            &_row {
                display: flex;
                align-items: stretch;
                flex-direction: column;
                gap: 22px;
            }
        }
    
        &__networks {
            display: flex;
            gap: 8px;
        }
    
        &__network {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 50px;
            height: 50px;
            border-radius: 2px;
            background: #fff;
            border: 1px solid #fff;
            transition: all 0.2s ease-in;
            
            &:hover {
                background: transparent;
            }
        }
    
        &__nav {
            flex-direction: column;
            gap: 16px;
            width: 100%;
            margin-bottom: 40px;
            &_item {
                max-width: 100%;
                &:not(:last-child) {
                    padding-right: 0;
                    border-right: none;
                    padding-bottom: 16px;
                    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
                }
            }
    
            &_acc {
                display: flex;
                align-items: center;
                justify-content: space-between;
                gap: 12px;
                margin-bottom: 0;
                pointer-events: all;
                transition: all 0.3s ease;
                &::after {
                    content: "";
                    display: inline-block;
                    width: 10px;
                    height: 10px;
                    border-bottom: 2px solid var(--akcent);
                    border-right: 2px solid var(--akcent);
                    transform: rotate(45deg);
                    transition: all 0.3s ease;
                }
                &.active {
                    margin-bottom: 20px;
                }
                &.active::after {
                    transform: rotate(225deg);
                }
            }
    
            & ul {
                max-height: 0;
                overflow: hidden;
                transition: all 0.3s ease;
            }
        }

    }
}