
// doctor
.doctor {
    &__row {
        display: flex;
        gap: 40px;
    }
    
    &__left {
        flex: 0 0 300px;
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    &__left_bottom {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    &__right {
        flex-grow: 1;
        max-width: calc(100% - 340px);
    }

    &__photo {
        border: 1px solid rgba(66, 133, 244, 0.1);
        border-radius: 2px;
        width: 100%;
        aspect-ratio: 1 / 1;
        background:  rgba(66, 133, 244, 0.05);
        position: relative;
        overflow: hidden;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: top;
        }
    }

    &__price {
        position: absolute;
        bottom: 0;
        right: 0;
        text-align: right;
        font-size: 16px;
        color: rgba(255, 255, 255, 0.5);
        padding: 32px 12px 12px;
        width: 222px;
        height: 94px;
        background: #ED4545;
      
        -webkit-mask-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="222" height="94" viewBox="0 0 222 94"><path d="M438 94C438 69.0696 414.927 45.1604 373.856 27.532C332.786 9.90355 277.082 0 219 0C160.918 0 105.214 9.90354 64.1436 27.532C23.0732 45.1604 0 69.0696 0 94H438Z" fill="black"/></svg>');
        -webkit-mask-repeat: no-repeat;
        -webkit-mask-size: contain;
      
        mask-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="222" height="94" viewBox="0 0 222 94"><path d="M438 94C438 69.0696 414.927 45.1604 373.856 27.532C332.786 9.90355 277.082 0 219 0C160.918 0 105.214 9.90354 64.1436 27.532C23.0732 45.1604 0 69.0696 0 94H438Z" fill="black"/></svg>');
        mask-repeat: no-repeat;
        mask-size: contain;

        & span {
            display: block;
            font-weight: 700;
            font-size: 20px;
            line-height: 110%;
            color: #fff;
            margin-top: 4px;
        }
    }

    &__ratings {
        border-radius: 2px;
        padding: 16px;
        background: #f6f9fe;

        & li {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 10px;

            &:not(:last-child) {
                padding-bottom: 12px;
                border-bottom: 1px solid rgba(66, 133, 244, 0.1);
                margin-bottom: 12px;
            }
        }

        &_estimation {
            display: flex;
            align-items: center;
            gap: 8px;
            font-weight: 500;
            font-size: 16px;
            line-height: 130%;
            color: #2e3749;
        }

        &_stars {
            display: flex;
            gap: 4px;
        }
    }

    &__title {
        color: var(--temnyy);
        text-align: left;
        padding-bottom: 32px;
        border-bottom: 1px solid rgba(60, 50, 50, 0.1);
        margin-bottom: 24px;
    }

    &__info {
        padding-bottom: 24px;
        border-bottom: 1px solid rgba(60, 50, 50, 0.1);
        margin-bottom: 24px;
        display: flex;
        flex-direction: column;
        gap: 14px;

        & li {
            display: flex;
            align-items: center;
            gap: 12px;

            & img {
                min-width: 24px;
                width: 24px;
                height: 24px;
                object-fit: contain;
            }
        }
    }

    &__shedule {
        & ul {
            display: flex;
            flex-direction: column;
            gap: 8px;

            & li {
                border: 1px solid rgba(66, 133, 244, 0.2);
                border-radius: 2px;
                padding: 20px 24px;
                background: rgba(66, 133, 244, 0.05);
                display: flex;
                align-items: center;
                justify-content: space-between;
                gap: 10px;
                & span {
                    font-weight: 500;
                    color: var(--osnovnoy);
                }
                &.weekend {
                    border: 1px solid rgba(237, 69, 69, 0.2);
                    background: rgba(237, 69, 69, 0.05);
                    & span {
                        color: var(--akcent);
                    }
                }
            }
        }
    }

    &__education {
        & ul {
            & li {
                &:not(:last-child) {
                    padding-bottom: 20px;
                    border-bottom: 1px solid rgba(60, 50, 50, 0.1);
                    margin-bottom: 20px;
                }
                & span {
                    display: block;
                    font-weight: 500;
                    font-size: 20px;
                    color: var(--osnovnoy);
                    position: relative;
                    padding-left: 16px;
                    margin-bottom: 16px;

                    &::before {
                        content: "";
                        display: block;
                        width: 8px;
                        height: 8px;
                        border-radius: 100%;
                        background: var(--osnovnoy);
                        position: absolute;
                        top: 8px;
                        left: 0;
                    }
                }
            }
        }
    }

    &__licenses {
        &_card {
            border-radius: 2px;
            padding: 10px;
            background: rgba(66, 133, 244, 0.1);
        }

        &_photo {
            display: block;
            margin-bottom: 8px;
        }

        &_picture {
            display: block;
            width: 100%;
            height: 100%;

            & img {
                width: 100%;
                height: 100%;
                max-height: 315px;
                object-fit: cover;
                object-position: top;
            }
        }

        &_label {
            border-radius: 2px;
            padding: 16px 20px;
            background: #fff;
            text-align: center;
        }

        &_paginations {
            margin-top: 24px;
        }
    }

    &__works {
        & ul {
            display: flex;
            flex-direction: column;
            gap: 20px;
            & li {
                position: relative;
                padding-left: 16px;
                &::before {
                    content: "";
                    display: block;
                    width: 8px;
                    height: 8px;
                    border-radius: 100%;
                    background: var(--akcent);
                    position: absolute;
                    top: 8px;
                    left: 0;
                }

                & a {
                    display: block;
                    text-decoration: underline;
                    color: #1f1f1f;
                    transition: all 0.2s ease-in;
                    &:hover {
                        color: var(--akcent);
                    }
                }
            }
        }
    }
}

// media doctor
@media (max-width: 1024px) {
    .doctor {
        &__row {
            flex-direction: column;
            gap: 0px;
        }
        &__left {
            display: contents;
        }
        &__right {
            display: contents;
        }
    
        &__photo {
            order: -2;
            width: 100%;
            max-width: 400px;
            margin: 0 auto;
            margin-bottom: 24px;
        }

        &__left_bottom {
            gap: 24px;
            margin-bottom: 160px;
        }
        &__section {
            display: contents;
        }
        &__title {
            text-align: center;
            padding-bottom: 0;
            border-bottom: none;
            margin-bottom: 32px;
            order: -3;
        }

        &__content {
            order: -1;
            margin-bottom: 16px;
        }

    }
}
@media (max-width: 768px) {
    .doctor {
        &__left_bottom {
            margin-bottom: 80px;
        }

        &__info {
            padding-bottom: 16px;
            margin-bottom: 16px;
            gap: 10px;
            & li {
                & img {
                    min-width: 18px;
                    width: 18px;
                    height: 18px;
                }
            }
        }
    
        &__shedule {
            & ul {
                & li {
                    padding: 16px 20px;
                }
            }
        }
    
        &__education {
            & ul {
                & li {
                    &:not(:last-child) {
                        padding-bottom: 16px;
                        margin-bottom: 16px;
                    }
                    & span {
                        font-size: 16px;
                        padding-left: 12px;
                        margin-bottom: 12px;
                        &::before {
                            width: 6px;
                            height: 6px;
                            top: 8px;
                        }
                    }
                }
            }
        }
    
        &__licenses {
            &_label {
                padding: 12px 16px;
            }
            &_paginations {
                margin-top: 14px;
                & button {
                    display: none;
                }
            }
        }
    
        &__works {
            & ul {
                gap: 16px;
                & li {
                    padding-left: 12px;
                    &::before {
                        width: 6px;
                        height: 6px;
                    }
                }
            }
        }
    }
}