// text-block
.text-block {

    & h2, & h3, & h4, & h5, & h6 {
        font-weight: 700;
        line-height: 110%;
        color: var(--temnyy);
        position: relative;
        padding-bottom: 20px;
        margin-bottom: 30px;
        &::after {
            content: "";
            display: block;
            width: 100px;
            height: 3px;
            background: var(--osnovnoy);
            position: absolute;
            bottom: 0;
            left: 0;
        }
        &:not(:first-child) {
            margin-top: 60px;
        }
    }
    & h2 {
        font-size: 48px;
    }
    & h3 {
        font-size: 36px;
    }
    & h4 {
        font-size: 24px;
    }
    & h2:empty, & h3:empty, & h4:empty, & h5:empty, & h6:empty {
        margin-bottom: 0;
    }

    & p {
        &:not(:last-child) {
            margin-bottom: 24px;
        }
    }
    & span {
        font-weight: 500;
    }
    & a:not(.phone-btn-white) {
        color: var(--temnyy);
        text-decoration: underline;
        transition: all 0.3s ease;
        &:hover {
            color: var(--akcent);
        }
    }

    & ul:not(.breadcrumbs) {
        display: flex;
        flex-direction: column;
        border: 1px solid rgba(237, 69, 69, 0.1);
        border-radius: 2px;
        padding: 24px;
        background: rgba(237, 69, 69, 0.04);
        margin-bottom: 24px;
        & li {
            display: flex;
            gap: 10px;
            &::before {
                content: "";
                display: block;
                min-width: 8px;
                width: 8px;
                height: 8px;
                border-radius: 100%;
                background-color: var(--akcent);
                margin-top: 8px;
            }
            &:nth-child(even)::before {
                background-color: var(--osnovnoy);
            }
            &:not(:last-child) {
                margin-bottom: 14px;
            }
        }
    }

    & ol {
        display: flex;
        flex-direction: column;
        list-style-type: none;
        counter-reset: item;
        border: 1px solid rgba(237, 69, 69, 0.1);
        border-radius: 2px;
        padding: 24px;
        background: rgba(237, 69, 69, 0.04);
        margin-bottom: 24px;
        & li {
            display: flex;
            gap: 12px;
            &::before {
                content: counter(item);
                counter-increment: item;
                display: block;
                min-width: 28px;
                width: 28px;
                height: 28px;
                border: 1px solid var(--akcent);
                border-radius: 2px;
                font-size: 16px;
                color: var(--akcent);
                display: flex;
                align-items: center;
                justify-content: center;
            }
            &:not(:last-child) {
                margin-bottom: 14px;
            }
        }
    }

    & img {
        border-radius: 2px;
        width: 100%;
        max-width: 480px;
        height: 100%;
        max-height: 480px;
        object-fit: cover;
        margin: 20px 0 20px 20px;
        float: right;
    }

    & blockquote {
        border: 1px solid var(--osnovnoy);
        border-radius: 2px;
        padding: 40px;
        padding-top: 96px;
        position: relative;
        overflow: hidden;
        &::after {
            content: "";
            display: block;
            width: 39px;
            height: 32px;
            background: url("./../img/icons/quote.svg") no-repeat center / contain;
            position: absolute;
            top: 40px;
            left: 40px;
        }
        &::before {
            content: "";
            display: block;
            width: calc(100% - 140px);
            height: 1px;
            background: rgba(66, 133, 244, 0.2);
            position: absolute;
            top: 55px;
            left: 99px;
        }

        & .blockquote {
            &__main {
                font-style: italic;
                color: var(--temnyy);
                padding-bottom: 20px;
                border-bottom: 1px solid rgba(60, 50, 50, 0.1);
                margin-bottom: 20px;
                & p:not(:last-child) {
                    margin-bottom: 16px;
                }
                & span {
                    font-weight: 600;
                }
            }
        
            &__author {
                display: flex;
                align-items: center;
                gap: 60px;
        
                &_left {
                    display: flex;
                    align-items: center;
                    gap: 20px;
                }
            
                &_photo {
                    display: block;
                    border-radius: 100px;
                    min-width: 80px;
                    width: 80px;
                    height: 80px;
                    overflow: hidden;
                    border: 1px solid rgba(66, 133, 244, 0.1);
                    background: rgba(66,133,244,0.05);
                    & img {
                        float: none;
                        margin: 0;
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
    
                &_fio {
                    font-weight: 700;
                    font-size: 24px;
                    line-height: 110%;
                    margin: 0;
                }
            
                &_item {
                    display: flex;
                    align-items: flex-start;
                    gap: 10px;
                    font-size: 16px;
                    & img {
                        float: none;
                        margin: 0;
                        min-width: 18px;
                        width: 18px;
                        height: 18px;
                        object-fit: contain;
                    }
                }
            }
        }
    }

    & .warning {
        display: flex;
        flex-direction: column;
        border: 1px solid var(--akcent);
        border-radius: 2px;
        padding: 28px;
        font-style: italic;
        margin-bottom: 24px;

        &__title {
            font-style: italic;
            font-weight: 700;
            font-size: 36px;
            line-height: 110%;
            color: var(--akcent);
            margin-bottom: 13px;
        }
    }

    &__form {
        border-radius: 2px;
        padding: 40px;
        background: url("./../img/text-block/form-bg.jpg") no-repeat center / cover;
        color: #fff;
        margin: 60px 0;
        &_title {
            font-weight: 700;
            font-size: 48px;
            line-height: 110%;
            margin-bottom: 16px;
        }
    }

    & table {
        display: block;
        width: 100%;
        border: none;
        border-radius: 2px;
        overflow: hidden;
        border-collapse: collapse; 
        margin-bottom: 24px;
        border: 1px solid rgba(66, 133, 244, 0.15);

        & thead {
            & th {
                border: none;
                padding: 20px;
                background: #f6f9fe;
                font-weight: 500;
                font-size: 20px;
                border-right: 1px solid rgba(66, 133, 244, 0.15);
                text-align: left;
            }
            & tr th:last-child {
                border-right: none;
            }
        }

        & tbody {
            & td {
                border-right: 1px solid rgba(66, 133, 244, 0.15);
                padding: 20px;
                font-size: 16px;
                vertical-align: top;
            }
            & tr td:last-child {
                border-right: none;
            }
            & tr:nth-child(even){
                background: #f6f9fe;
            }
        }
    }


    &__literature {
        &_title {
            font-weight: 700;
            font-size: 26px;
            line-height: 140%;
            color: var(--tekst);
            margin-bottom: 40px;
        }

        & ol {
            display: flex;
            flex-direction: column;
            gap: 10px;
            padding-bottom: 30px;
            border-bottom: 1px solid #c7d6dc;
            margin-bottom: 30px;
            list-style-type: none;
            counter-reset: item;
            & li {
                display: flex;
                gap: 12px;
                color: var(--tegi);
                opacity: 0.85;
                &::before {
                    content: counter(item);
                    counter-increment: item;
                    display: block;
                    min-width: max-content;
                    border: none;
                    font-weight: 700;
                    font-size: 20px;
                    line-height: 140%;
                    color: var(--color-temno-zelenyy);
                    opacity: 0.4;
                }
            }
        }

        &_bottom {
            display: flex;
            gap: 20px;
            justify-content: space-between;
        }

        &_item {
            font-weight: 700;
            font-size: 18px;
            line-height: 140%;
            color: var(--tekst);
            display: flex;
            gap: 15px;
            justify-content: space-between;
            & p {
                margin: 0 !important;
            }
        }

        &_share {
            font-weight: 500;
            font-size: 16px;
            color: var(--tekst-svetlyy);
        }

        &_links {
            display: flex;
            gap: 6px;
        }

        &_link {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 26px;
            height: 26px;
            border-radius: 5px;
            background-color: var(--color-zelenyy);
        }
    }
}

.authors {
    display: flex;
    gap: 20px;
    margin-top: 60px;
}
.author {
    flex: 1  0 40%;
    border: 1px solid rgba(66, 133, 244, 0.1);
    padding: 24px;
    background: #f6f9fe;

    &__top {
        font-size: 16px;
        line-height: 130%;
        color: rgba(60, 50, 50, 0.6);
        padding-bottom: 16px;
        border-bottom: 1px solid rgba(60, 50, 50, 0.1);
        margin-bottom: 16px;
        & span {
            font-style: italic;
            font-weight: 500;
            color: var(--osnovnoy);
        }
    }

    &__doc {
        display: flex;
        align-items: center;
        gap: 16px;
        margin-bottom: 16px;
    }

    &__photo {
        display: block;
        border-radius: 100px;
        min-width: 80px;
        width: 80px;
        height: 80px;
        overflow: hidden;
        border: 1px solid rgba(66, 133, 244, 0.1);
        background: #fff;
        & img {
            float: none;
            margin: 0 !important;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__fio {
        font-weight: 700;
        font-size: 24px;
        line-height: 110%;
        margin: 0;
    }

    &__item {
        display: flex;
        align-items: flex-start;
        gap: 10px;
        font-size: 16px;
        & img {
            float: none;
            margin: 0 !important;
            min-width: 18px;
            width: 18px;
            height: 18px;
            object-fit: contain;
        }
    }
}


.literature {
    padding-top: 32px;
    border-top: 1px solid rgba(60, 50, 50, 0.1);
    margin-top: 32px;
    margin-bottom: 32px;

    &__title {
        font-weight: 700;
        font-size: 24px;
        line-height: 110%;
        color: var(--temnyy);
        margin-bottom: 24px;
    }

    & ul {
        list-style: none;

        & li {
            position: relative;
            padding-left: 20px;
            margin-bottom: 15px;

            &::before {
                content: "";
                display: block;
                width: 8px;
                height: 8px;
                border-radius: 100%;
                background: var(--akcent);
                position: absolute;
                top: 8px;
                left: 0;
            }

            & a {
                text-decoration: underline;
                color: var(--temnyy);
                transition: all 0.2s ease-in;
                &:hover {
                    color: var(--akcent);
                }
            }
        }
    }
}

.share {
    border: 1px solid rgba(66, 133, 244, 0.1);
    padding: 20px 20px 20px 28px;
    background: #f6f9fe;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;

    &__networks {
        display: flex;
        gap: 8px;
    }

    &__network {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 56px;
        height: 56px;
        border-radius: 2px;
        background: #fff;
        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.08);
        transition: all 0.3s ease;
        
        & svg path {
            transition: all 0.3s ease;
        }

        &:first-child:hover {
            background: #ED812B;
            & svg path {
                fill: #fff;
            }
        }
        &:last-child:hover {
            background: #27a7e7;
            & svg path {
                fill: #fff;
            }
        }
    }
}



// media text-block
@media (max-width: 900px) {
    .text-block {
        & img {
            float: none;
            margin: 24px 0;
            max-width: 100%;
        }
    }
}
@media (max-width: 768px) {
    .text-block {
        & h2 {
            font-size: 26px;
        }
        & h3 {
            font-size: 24px;
        }
        & h4 {
            font-size: 20px;
        }
        & p {
            &:not(:last-child) {
                margin-bottom: 12px;
            }
        }
        & ul:not(.breadcrumbs) {
            padding: 20px;
            margin-bottom: 12px;
            & li {
                &::before {
                    min-width: 6px;
                    width: 6px;
                    height: 6px;
                }
                &:not(:last-child) {
                    margin-bottom: 10px;
                }
            }
        }   
        & ol {
            padding: 20px;
            margin-bottom: 12px;
            & li {
                &::before {
                    min-width: 24px;
                    width: 24px;
                    height: 24px;
                    font-size: 14px;
                }
                &:not(:last-child) {
                    margin-bottom: 10px;
                }
            }
        }
        & img {
            margin: 12px 0;
            max-height: 220px;
        }
        & blockquote {
            padding: 20px;
            padding-top: 68px;
            margin-bottom: 12px;
            &::after {
                top: 20px;
                left: 20px;
            }
            &::before {
                width: calc(100% - 100px);
                top: 35px;
                left: 79px;
            }
    
            & .blockquote {
                &__main {
                    padding-bottom: 16px;
                    margin-bottom: 16px;
                }
            
                &__author {
                    flex-direction: column;
                    align-items: stretch;
                    gap: 12px;
                    &_left {
                        gap: 10px;
                    }
                    &_photo {
                        min-width: 60px;
                        width: 60px;
                        height: 60px;
                    }
                    &_fio {
                        font-size: 20px;
                        margin: 0;
                    }
                }
            }
        }  
        & .warning {
            padding: 20px;
            margin-bottom: 12px;
    
            &__title {
                font-size: 24px;
                margin-bottom: 8px;
            }
        }
    
        &__form {
            padding: 60px 20px;
            &_title {
                font-size: 26px;
            }
            & button {
                padding: 16px;
                width: 100%;
                margin-top: 30px;
            }
        }
    
        & table {
            overflow-x: auto;
            max-width: 100%;
            & thead th, & tbody td {
                min-width: 243px;
                padding: 16px;
            }
            &::-webkit-scrollbar {
                height: 3px;
            }
            &::-webkit-scrollbar-track {
                background: transparent;
            }
            &::-webkit-scrollbar-thumb {
                background: var(--osnovnoy);
                border-radius: 10px;
            }
            & thead th {
                font-size: 16px;
            }
            & tbody td {
                font-size: 14px;
            }
        }

        &__literature {
            &_title {
                font-size: 20px;
                margin-bottom: 24px;
            }
            & ol {
                margin-bottom: 14px;
                & li {
                    font-size: 14px;
                    &::before {
                        font-size: 18px;
                    }
                }
            }
            &_bottom {
                flex-direction: column;
                gap: 14px;
            }
            &_item {
                padding-bottom: 14px;
                font-size: 14px;
                border-bottom: 1px solid #c7d6dc;
                align-items: center;
            }
            &_share {
                font-size: 14px;
            }
            &_links {
                display: flex;
                gap: 10px;
            }
            &_link {
                width: 32px;
                height: 32px;
            }
        }
    }

    .authors {
        flex-direction: column;
        gap: 10px;
        margin-top: 40px;
    }
    .author {
        padding: 20px;
        &__top {
            font-size: 14px;
            padding-bottom: 12px;
            margin-bottom: 12px;
        }
        &__doc {
            gap: 12px;
            margin-bottom: 12px;
        }
        &__photo {
            min-width: 60px;
            width: 60px;
            height: 60px;
        }
        &__fio {
            font-size: 20px;
        }
    }

    .literature {
        padding-top: 20px;
        margin-top: 20px;
        margin-bottom: 20px;
        &__title {
            margin-bottom: 20px;
        }
        & ul {
            list-style: none;
            & li {
                margin-bottom: 10px;
                &::before {
                    width: 6px;
                    height: 6px;
                }
            }
        }
    }
    
    .share {
        padding: 20px;
        background: #f6f9fe;
        flex-direction: column;
        align-items: stretch;
        gap: 10px;
        &__network {
            width: 50px;
            height: 50px;
        }
    }
}