
// articles
.articles {
    &__card {
        display: block;
        border: 1px solid rgba(66, 133, 244, 0.1);
        border-radius: 2px;
        background: rgba(66, 133, 244, 0.04);

        &:hover {
            & .articles__card_name {
                color: var(--osnovnoy);
            }
        }

        &_img {
            display: block;
            width: 100%;
            & img {
                width: 100%;
                height: 100%;
                max-height: 200px;
                object-fit: cover;
                border-radius: 2px;
            }
        }

        &_inner {
            padding: 24px;
        }

        &_name {
            font-weight: 700;
            font-size: 24px;
            line-height: 110%;
            color: var(--temnyy);
            margin-bottom: 12px;
            transition: all 0.2s ease-in;
        }

        &_text {
            color: rgba(60, 50, 50, 0.7);
            margin-bottom: 20px;
        }

        &_date {
            color: var(--osnovnoy);
        }
    }
}


// media articles
@media (max-width: 768px) {
    .articles {
        &__card {
            &_inner {
                padding: 20px;
            }
            &_name {
                font-size: 20px;
            }
            &_text {
                margin-bottom: 16px;
            }
        }
    }
}