
// location
.location {
    &__left {
        & p {
            margin-bottom: 24px;
        }

        & button {
            width: 100%;
        }
    }

    &__maps {
        border-radius: 2px;
        width: 100%;
        height: 100%;
        min-height: 411px;
        max-height: 411px;
        position: relative;
        overflow: hidden;
        margin-bottom: 24px;
    }

    &__map {
        position: absolute;
        inset: 0;
        z-index: 2;
        width: 100%;
        height: 100%;
    }

    &__right {
        width: 100%;
    }

    &__item {
        &:not(:last-child) {
            padding-bottom: 24px;
            border-bottom: 1px solid rgba(60, 50, 50, 0.1);
            margin-bottom: 24px;
        }
        &_name {
            font-weight: 700;
            font-size: 24px;
            line-height: 110%;
            color: var(--temnyy);
            margin-bottom: 20px;
        }

        &_border {
            border: 1px solid var(--osnovnoy);
            border-radius: 2px;
            padding: 28px;

            & span {
                display: block;
                font-weight: 700;
                font-size: 20px;
                line-height: 110%;
                color: var(--osnovnoy);
                margin-bottom: 8px;
            }
        }

        &_time {
            & li {
                &:not(:last-child) {
                    padding-bottom: 20px;
                    border-bottom: 1px solid rgba(60, 50, 50, 0.1);
                    margin-bottom: 20px;
                }
                
                & span {
                    position: relative;
                    padding-left: 30px;
                    &::before {
                        content: "";
                        display: block;
                        width: 20px;
                        height: 20px;
                        background: url("./../img/icons/clock.svg") no-repeat center / contain;
                        position: absolute;
                        top: 0;
                        left: 0;
                    }
                }
            }
        }
    }
}

// media location
@media (max-width: 1100px) {
    .location {
        &__left {
            display: contents;
            & button {
                order: 3;
            }
        }
    }
}
@media (max-width: 768px) {
    .location {
        &__left {
            & p {
                margin-bottom: 0;
            }
        }

        &__maps {
            min-height: 300px;
            max-height: 300px;
        }
        
        &__right {
            padding-top: 20px;
            border-top: 1px solid rgba(60, 50, 50, 0.1);
        }

        &__item {
            &:not(:last-child) {
                padding-bottom: 20px;
                margin-bottom: 20px;
            }
            &_name {
                margin-bottom: 16px;
            }

            &_border {
                padding: 20px;
            }

            &_time {
                & li {
                    &:not(:last-child) {
                        padding-bottom: 12px;
                        margin-bottom: 12px;
                    }
                }
            }
        }
    }
}