
// navigation
.navigation {
    border-radius: 2px;
    padding: 40px;
    box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.08);
    background: #fff;

    &__title {
        font-weight: 700;
        font-size: 36px;
        line-height: 110%;
        color: var(--temnyy);
        margin-bottom: 32px;
    }

    &__list {
        columns: 2;
        column-gap: 20px;
        list-style: none;

        & li {
            position: relative;
            padding-left: 20px;
            margin-bottom: 15px;

            &::before {
                content: "";
                display: block;
                width: 8px;
                height: 8px;
                border-radius: 100%;
                background: var(--akcent);
                position: absolute;
                top: 8px;
                left: 0;
            }

            & a {
                text-decoration: underline;
                color: var(--temnyy);
                transition: all 0.2s ease-in;
                &:hover {
                    color: var(--akcent);
                }
            }
        }
    }
}

// media navigation
@media (max-width: 768px) {
    .navigation {
        padding: 20px;
        &__title {
            font-size: 20px;
            margin-bottom: 20px;
        }
        &__list {
            columns: 1;
            & li {
                padding-left: 16px;
                margin-bottom: 10px;
                &::before {
                    width: 6px;
                    height: 6px;
                }
            }
        }
    }
}

.article-navigation {
    border-radius: 2px;
    padding: 24px;
    box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.08);
    background: #fff;

    &__title {
        font-size: 24px;
        margin-bottom: 24px;
    }

    &__list {
        columns: 1;
        list-style: none;

        & li {
            position: relative;
            padding-left: 20px;
            margin-bottom: 12px;

            &::before {
                content: "";
                display: block;
                width: 8px;
                height: 8px;
                border-radius: 100%;
                background: var(--akcent);
                position: absolute;
                top: 8px;
                left: 0;
            }

            & a {
                text-decoration: underline;
                color: var(--temnyy);
                transition: all 0.2s ease-in;
                &:hover {
                    color: var(--akcent);
                }
            }
        }
    }
}



.aside {
    position: sticky;
    top: 100px;
    padding-bottom: 160px;

}

.aside__block {
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(60, 50, 50, 0.1);
    margin-bottom: 20px;
}

.aside__plashka {
    padding: 24px;
    background: url("./../img/text-block/plashka.jpg") no-repeat center / cover;
    display: flex;
    flex-direction: column;
    gap: 20px;
    font-weight: 700;
    font-size: 24px;
    line-height: 110%;
    color: #fff;
}

.aside__btns {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.dutys {
    border: 1px solid var(--akcent);
    border-radius: 2px;
    padding: 24px;

    &__title {
        font-weight: 700;
        font-size: 20px;
        line-height: 110%;
        color: var(--akcent);
        margin-bottom: 24px;
    }
}

.duty {
    &__doc {
        display: flex;
        align-items: center;
        gap: 10px;
        margin-bottom: 14px;
    }

    &__photo {
        display: block;
        border-radius: 100px;
        min-width: 60px;
        width: 60px;
        height: 60px;
        overflow: hidden;
        border: 1px solid rgba(66, 133, 244, 0.1);
        background: #fff;
        & img {
            float: none;
            margin: 0 !important;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__fio {
        font-weight: 700;
        font-size: 20px;
        line-height: 110%;
    }

    &__item {
        display: flex;
        align-items: flex-start;
        gap: 8px;
        font-size: 16px;
        & img {
            float: none;
            margin: 0 !important;
            min-width: 18px;
            width: 18px;
            height: 18px;
            object-fit: contain;
        }
    }
}

@media (max-width: 1024px) {

    .aside {
        position: static;
        padding-bottom: 0;
    }
    
    .aside__block {
        padding-bottom: 12px;
        margin-bottom: 12px;

        &-docs {
            padding-bottom: 0;
            margin-bottom: 0;
            border: none;
        }
    }

    .aside__mob {
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 5;
        padding: 20px 10px;
        box-shadow: 0 -5px 20px 0 rgba(0, 0, 0, 0.08);
        background: #fff;
    }
    
    .aside__plashka {
        padding: 0;
        background: transparent;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        gap: 10px;
        font-weight: 500;
        font-size: 16px;
        color: var(--osnovnoy);
        & svg path {
            fill: var(--osnovnoy);
        }
    }
    
    .aside__btns {
        flex-direction: row;
        gap: 10px;
        & > * {
            padding: 14px;
            flex-grow: 1;
        }
    }
    
    .dutys {
        padding: 20px;
        &__title {
            margin-bottom: 20px;
        }
    }
    
    .duty {
        &__doc {
            margin-bottom: 12px;
        }
        &__photo {
            min-width: 52px;
            width: 52px;
            height: 52px;
        }
        &__fio {
            font-size: 18px;
        }
    }
}