

// faq
.faq {
    &__list {
        display: flex;
        flex-direction: column;
        gap: 12px;
    }

    &__item {
        border: 1px solid rgba(60, 50, 50, 0.1);
        border-radius: 2px;
        padding: 24px;
        box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
        background: #fff;
    }

    &__question {
        font-weight: 700;
        font-size: 20px;
        line-height: 110%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
    }

    &__btn {
        display: block;
        border-radius: 100%;
        width: 40px;
        min-width: 40px;
        height: 40px;
        background: rgba(237, 69, 69, 0.1);
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all 0.3s ease;
        
        &::after {
            content: "";
            display: block;
            width: 10px;
            height: 10px;
            border-bottom: 2px solid var(--akcent);
            border-right: 2px solid var(--akcent);
            transform: rotate(45deg);
            margin-left: 1px;
            margin-bottom: 2px;
            transition: all 0.3s ease;
        }
        &:hover {
            background:  rgba(237, 69, 69, 1);
            &::after {
                border-bottom: 2px solid #fff;
                border-right: 2px solid #fff;
            }
        }
    }

    &__answer {
        padding-top: 14px;
        border-top: 1px solid rgba(60, 50, 50, 0.1);
        margin-top: 14px;
    }
}

// media faq
@media (max-width: 768px) {
    .faq {
        &__item {
            padding: 20px;
        }
        &__question {
            font-size: 18px;
        }
        &__btn {
            width: 32px;
            min-width: 32px;
            height: 32px;
        }
    }
}