
// stocks
.stocks {
    &__card {
        display: block;
        border: 1px solid rgba(237, 69, 69, 0.1);
        border-radius: 2px;
        background: rgba(237, 69, 69, 0.04);

        &_img {
            display: block;
            width: 100%;
            & img {
                width: 100%;
                height: 100%;
                max-height: 200px;
                object-fit: cover;
                border-radius: 2px;
            }
        }

        &_title {
            border-radius: 0 2px 2px 0;
            padding: 20px;
            background: var(--akcent);
            font-weight: 700;
            font-size: 24px;
            line-height: 110%;
            text-align: center;
            color: #fff;
        }

        &_inner {
            padding: 24px;
        }

        &_text {
            color: rgba(60, 50, 50, 0.7);
            margin-bottom: 16px;
        }

        &_btn {
            width: 100%;
        }
    }
}

// media stocks
@media (max-width: 768px) {
    .stocks {
        &__card {
            &_img {
                display: none;
            }
            &_title {
                padding: 16px;
                font-size: 20px;
            }
            &_inner {
                padding: 20px;
            }
            &_text {
                color: var(--temnyy);
            }
        }
    }
}