
// site-map
.site-map {
    &__items {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 60px 40px;
    }

    &__accordion, &__content {
        display: contents;
    }

    &__title {
        display: block;
        font-weight: 700;
        font-size: 36px;
        line-height: 110%;
        color: var(--temnyy);
        width: fit-content;
        margin-bottom: 24px;
        transition: all 0.2s ease-in;
        &:hover {
            color: var(--akcent);
        }
    }

    &__list {
        list-style: none;

        & li {
            position: relative;
            padding-left: 20px;
            margin-bottom: 15px;

            &::before {
                content: "";
                display: block;
                width: 8px;
                height: 8px;
                border-radius: 100%;
                background: var(--akcent);
                position: absolute;
                top: 8px;
                left: 0;
            }

            & a {
                text-decoration: underline;
                color: var(--temnyy);
                transition: all 0.2s ease-in;
                &:hover {
                    color: var(--akcent);
                }
            }
        }
    }
}

// media site-map
@media (max-width: 1200px) {
    .site-map {
        &__items {
            grid-template-columns: repeat(3, 1fr);
            gap: 60px 40px;
        }
    }
}
@media (max-width: 1000px) {
    .site-map {
        &__items {
            grid-template-columns: repeat(2, 1fr);
            gap: 60px 40px;
        }
    }
}
@media (max-width: 768px) {
    .site-map {
        &__items {
            grid-template-columns: repeat(1, 1fr);
            gap: 16px;
        }
    
        &__accordion, &__content {
            display: block;
        }

        &__item {
            border-bottom: 1px solid rgba(60, 50, 50, 0.1);
        }

        &__accordion {
            padding-right: 20px;
            position: relative;
            &::after {
                content: "";
                display: block;
                width: 10px;
                height: 10px;
                border-bottom: 2px solid var(--akcent);
                border-right: 2px solid var(--akcent);
                transform: rotate(45deg);
                position: absolute;
                top: 5px;
                right: 0;
            }

            &.accordion--active::after {
                transform: rotate(225deg);
            }
        }
    
        &__title {
            font-size: 20px;
            margin-bottom: 0;
            padding-bottom: 16px;
        }
    
        &__list {
            list-style: none;
    
            & li {
                position: relative;
                padding-left: 20px;
                margin-bottom: 15px;
    
                &::before {
                    content: "";
                    display: block;
                    width: 8px;
                    height: 8px;
                    border-radius: 100%;
                    background: var(--akcent);
                    position: absolute;
                    top: 8px;
                    left: 0;
                }
    
                & a {
                    text-decoration: underline;
                    color: var(--temnyy);
                    transition: all 0.2s ease-in;
                    &:hover {
                        color: var(--akcent);
                    }
                }
            }
        }
    }
}