
// stages
.stages {
    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        margin-bottom: 40px;

        & .title-h2 {
            margin-bottom: 0;
        }
    }

    &__swiper-slide {
        height: auto;
    }

    &__card {
        border: 1px solid var(--osnovnoy);
        border-radius: 2px;
        padding: 40px;
        background: #fff;
        display: flex;
        gap: 40px;
        height: 100%;

        &_text {
            flex: 1 0 40%;
            color: rgba(31, 31, 31, 0.6);
            padding-bottom: 62px;
        }

        &_title {
            font-weight: 700;
            font-size: 36px;
            line-height: 110%;
            color: var(--temnyy);
            margin-bottom: 12px;
        }
        
        &_img {
            flex: 1 0 40%;
            display: block;
            width: 100%;
            height: 100%;

            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 2px;
                max-height: 280px;
            }
        }
    }

    &__swiper-btns {
        position: absolute;
        bottom: 40px;
        left: 40px;
    }
}

// media stages
@media (max-width: 768px) {
    .stages {
        &__top {
            flex-direction: column;
            align-items: stretch;
            gap: 30px;
            padding-bottom: 16px;
            border-bottom: 1px solid rgba(66, 133, 244, 0.1);
            margin-bottom: 16px;
            & .title-h2 {
                margin-bottom: 0;
            }
        }

        & .swiper-pagination{
            display: flex;
        }
    
        &__card {
            padding: 20px;
            flex-direction: column;
            gap: 16px;
            &_text {
                flex-basis: auto;
                padding-bottom: 0;
            }
            &_title {
                font-size: 20px;
                margin-bottom: 8px;
            }
            &_img {
                flex-basis: auto;
                & img {
                    min-height: 200px;
                    max-height: 200px;
                }
            }
        }
        &__swiper-btns {
            display: none;
        }
    } 
}