
// error
.error {
    padding: 160px 0;
    &__wrap {
        width: 100%;
        max-width: 640px;
        margin: 0 auto;
    }

    &__number {
        text-align: center;
        margin-bottom: 60px;
    }

    &__title {
        font-weight: 700;
        font-size: 36px;
        line-height: 110%;
        text-align: center;
        color: var(--temnyy);
        margin-bottom: 16px;
    }

    &__subtitle {
        text-align: center;
        padding-bottom: 24px;
        border-bottom: 1px solid rgba(60, 50, 50, 0.1);
        margin-bottom: 24px;
    }

    &__links {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        margin-bottom: 28px;

        & li {
            position: relative;
            padding-left: 20px;

            &::before {
                content: "";
                display: block;
                width: 8px;
                height: 8px;
                border-radius: 100%;
                background: var(--akcent);
                position: absolute;
                top: 8px;
                left: 0;
            }

            & a {
                text-decoration: underline;
                color: var(--temnyy);
                transition: all 0.2s ease-in;
                &:hover {
                    color: var(--akcent);
                }
            }
        }
    }

    &__btn {
        width: 100%;
    }
}

// media error
@media (max-width: 768px) {
    .error {
        padding: 60px 0 80px;
        &__wrap {
            max-width: 100%;
        }
    
        &__number {
            text-align: center;
            margin-bottom: 30px;
            & svg {
                max-width: 75%;
            }
        }
    
        &__title {
            font-size: 29px;
        }
    
        &__subtitle {
            padding-bottom: 20px;
            margin-bottom: 20px;
        }
    
        &__links {
            flex-direction: column;
            align-items: stretch;
            gap: 16px;
            margin-bottom: 24px;
    
            & li {
                &::before {
                    width: 6px;
                    height: 6x;
                }
            }
        }
    }
}