
// reviews
.reviews {
    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        margin-bottom: 40px;
        & .title-h2 {
            margin-bottom: 0;
        }
    }

    &__card {
        height: auto;
        border: 1px solid var(--osnovnoy);
        border-radius: 2px;

        &_top {
            padding: 28px;
            background: rgba(66, 133, 244, 0.05);
        }

        &_fio {
            font-weight: 700;
            font-size: 24px;
            line-height: 110%;
            color: var(--temnyy);
            margin-bottom: 12px;
        }

        &_city {
            padding-left: 21px;
            position: relative;
            padding-bottom: 14px;
            border-bottom: 1px solid rgba(60, 50, 50, 0.1);
            margin-bottom: 14px;
            &::before {
                content: "";
                display: block;
                width: 13px;
                height: 18px;
                background: url("./../img/icons/location.svg") no-repeat center / contain;
                position: absolute;
                top: 0;
                left: 0;
            }
        }

        &_label {
            font-size: 16px;
            line-height: 130%;
            color: rgba(60, 50, 50, 0.6);
            margin-bottom: 6px;
        }

        &_content {
            padding: 28px;
        }

        &_text {
            max-height: 120px;
            overflow: hidden;
            margin-bottom: 14px;
        }

        &_picture {
            display: block;
            width: 100%;
            height: 100%;

            & img {
                display: block;
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: top;
                max-height: 450px;
            }
        }
    }

    &__paginations {
        margin-top: 24px;
    }


    &__selects {
        display: flex;
        gap: 44px;
        padding-bottom: 40px;
        border-bottom: 1px solid rgba(60, 50, 50, 0.1);
        margin-bottom: 40px;
    }
    &__select {
        flex-grow: 1;
    }
}

// media reviews
@media (max-width: 768px) {
    .reviews {
        overflow: hidden;
        &__top {
            flex-direction: column;
            align-items: stretch;
            gap: 30px;
            margin-bottom: 20px;
            & .title-h2 {
                margin-bottom: 0;
            }
            & .tab-btn {
                flex-grow: 1;
            }
        }

        &__swiper {
            overflow: visible;
        }
    
        &__card {
            &_top {
                padding: 20px;
            }
            &_fio {
                font-size: 20px;
                margin-bottom: 8px;
            }
    
            &_city {
                padding-left: 19px;
                padding-bottom: 12px;
                margin-bottom: 12px;
                &::before {
                    width: 11px;
                    height: 16px;
                }
            }
            &_label {
                font-size: 14px;
                margin-bottom: 4px;
            }
            &_content {
                padding: 20px;
            }
            &_text {
                max-height: 180px;
            }
    
            &_picture {
                & img {
                    max-height: 300px;
                }
            }
        }
    
        &__paginations {
            margin-top: 14px;
        }
        &__swiper-button-next, &__swiper-button-prev {
            display: none;
        }


        &__selects {
            flex-direction: column;
            gap: 24px;
            padding-bottom: 30px;
            margin-bottom: 30px;
        }
    }
}