/* Roboto */
@font-face {
    font-family: "Roboto";
    font-style: normal;
    font-display: swap;
    font-weight: 400;
    src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff"), local("Roboto");
}
@font-face {
    font-family: "Roboto";
    font-style: italic;
    font-display: swap;
    font-weight: 400;
    src: url("../fonts/Roboto-Italic.woff2") format("woff2"), url("../fonts/Roboto-Italic.woff") format("woff"), local("Roboto");
}
