/* header  */

.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 100;
    background-color: #fff;
    box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.08);
}

.header__logo {
    display: block;
    overflow: hidden;
    max-width: 255px;
    max-height: 60px;
    min-width: 200px;
    width: 100%;
}

.logo__picture {
    width: 100%;
    height: 100%;
    & img {
        width: 100%;
        height: 100%;
        max-width: 255px;
    }
}

.header__main, .header__top {
    &::-webkit-scrollbar {
        width: 0;
        height: 0;
    }
}

.main {
    margin-top: 188px;
}


/* header__top  */

.header__top {
    padding: 30px 0 20px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    border-bottom: 1px solid rgba(60, 50, 50, 0.1);

    opacity: 1;
    visibility: visible;
    transform: translateY(0);
    transition: all 0.3s ease;

    &.hide {
        transform: translateY(-150px);
        opacity: 0;
        visibility: hidden;
        padding: 5px 0;
    }
}

.header__info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
}

.header__info_text {
    font-size: 16px;
    line-height: 130%;
    color: rgba(60, 50, 50, 0.4);
    margin-bottom: 8px;
}

.header__info_link {
    font-weight: 500;
    font-size: 18px;
    line-height: 130%;
    color: var(--temnyy);
    transition: all 0.2s ease-in;

    & button {
        font-weight: 500;
        color: var(--akcent);
        text-decoration: underline;
        transition: all 0.2s ease-in;
        
        &:hover {
            color: rgba(237, 69, 69, 0.6);
        }
    }
}

a.header__info_link:hover {
    color: var(--osnovnoy);
    text-decoration: underline;
}

.header__btns {
    display: flex;
    gap: 12px;
}
.header__btn-search {
    padding: 21px;
    aspect-ratio: 1/1;
}


/* header__main */
.header__main {
    background: #fff;
}

.header__menu {
    display: contents;
}


.header__nav > ul {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
}
.header__nav_item {
    & a {
        display: block;
        padding: 20px 0 30px;
        font-weight: 500;
        font-size: 18px;
        line-height: 130%;
        color: var(--temnyy);
        transition: all 0.2s ease-in;
        
        &:hover {
            color: var(--akcent);
        }
    }

    &.hide-item {
        position: relative;
        padding-right: 24px;

        &::after {
            content: "";
            display: block;
            width: 10px;
            height: 10px;
            border-right: 2px solid var(--akcent);
            border-bottom: 2px solid var(--akcent);
            transform: rotate(45deg);
            cursor: pointer;
            position: absolute;
            top: 28px;
            bottom: 0;
            right: 0;
            transition: all 0.2s ease-in;
            
        }

        &:hover {
            &::after {
                transform: rotate(225deg);
            }
            .header__submenu {
                pointer-events: all;
                opacity: 1;
                transform: translateY(0);
            }
        }
    }
}


/*  header__submenu */
.header__submenu {
    z-index: 100;
    position: absolute;
    top: 100%;
    left: 0px;
    border-top: 1px solid rgba(60, 50, 50, 0.1);
    border-radius: 0 0 2px 2px;
    padding: 16px;
    box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.08);
    background: #fff;
    min-width: 308px;

    pointer-events: none;
    opacity: 0;
    transform: translateY(-50px);
    transition: all 0.3s ease;
    

    & a {
        display: block;
        padding: 8px 0;
        width: 100%;
    }
}
.hide-item--active {
    .header__submenu {
        display: block;
    }
}
.hide-item--active > a {
    color: var(--akcent);
    &::after {
        transform: rotate(225deg);
    }
}





/* mobile__wrapper */
.header-mobile__wrapper {
    display: none;
}

.header-mobile__search {
    position: relative;
}

.header-mobile__burger-btns {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 15px;
}

.header__networks {
    display: flex;
    gap: 8px;
}
.header__network {
    width: 50px;
    height: 50px;
    border-radius: 2px;
    fill: #fff;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.08);
    display: flex;
    align-items: center;
    justify-content: center;
}

.header-mobile__info {
    order: 5;
}

.header-mobile__phone {
    display: flex;
    align-items: center;
    gap: 8px;
    font-weight: 700;
    font-size: 16px;
    line-height: 130%;
    letter-spacing: -0.02em;
    color: var(--temnyy);
    vertical-align: middle;
    transition: all 0.2s ease-in;
    
    &:hover {
        color: var(--akcent);
    }
}


/* burger__menu */
.burger__menu {
    width: 40px;
    height: 40px;
    border-radius: 2px;
    background: var(--osnovnoy);
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}
.ham {
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
    transition: transform 400ms;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.hamRotate.active {
    transform: rotate(45deg);
}
.hamRotate180.active {
    transform: rotate(180deg);
}
.line {
    fill:none;
    transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
    stroke:#fff;
    stroke-width:5.5;
    stroke-linecap:round;
}
.ham4 .top {
    stroke-dasharray: 40 121;
}
.ham4 .bottom {
    stroke-dasharray: 40 121;
}
.ham4.active .top {
    stroke-dashoffset: -68px;
}
.ham4.active .bottom {
    stroke-dashoffset: -68px;
}


// media header-1
@media (max-width: 1260px) {
    .main {
        margin-top: 100px;
    }
    .header-mobile__wrapper {
        display: block;
    }

    .header__info_item, .header__nav,  .header__btns  {
        display: none;
    }
    .header__main {
        padding: 0;
    }
    .header__top {
        padding: 20px 0;
        border: none;
        gap: 16px;
    }

    .header__menu {
        display: none;
    }
    .header__menu--active {
        position: fixed;
        top: 100px;
        right: 0;
        width: 100%;
        z-index: 10;
        height: calc(100% - 100px);
        overflow-y: auto;
        padding: 40px 10px;
        background-color: #fff;
        display: flex;
        flex-direction: column;
        gap: 30px;
        border-top: 1px solid rgba(66, 133, 244, 0.2);
        &::-webkit-scrollbar {
            width: 3px;
        }
        &::-webkit-scrollbar-track {
            background: #DCDCDD;
            border-radius: 3px;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 3px;
            background: var(--akcent);
        }

        & .header__nav {
            display: block;
            opacity: 1 !important;
            order: 1;
        }
        & .header__btn {
            width: 100%;
        }
        & .header__info {
            display: flex;
            flex-direction: column;
            align-items: stretch;
            gap: 22px;
            order: 3;
            margin-bottom: 30px;
            & .header__info_item {
                display: block;
            }
        }
    }





    .header__nav > ul {
        flex-direction: column;
        align-items: stretch;
        gap: 0;
    }
    .header__nav_item {
        border-bottom: 1px solid rgba(60, 50, 50, 0.1);
        & a {
            display: block;
            width: fit-content;
            font-weight: 700;
            font-size: 20px;
            padding: 16px 0;
        }
    
        &.hide-item {
            padding-right: 40px;
            &::after {
                top: 22px;
                right: 4px;
            }
            &:hover {
                &::after {
                    transform: rotate(45deg);
                }
            }

            &.hide-item--active {
                &::after {
                    transform: rotate(225deg);
                }
                .header__submenu {
                    max-height: none;
                    pointer-events: all;
                    opacity: 1;
                    transform: translateY(0px);
                    margin-bottom: 20px;
                }
            }
        }
    }

    .header__submenu {
        z-index: 100;
        position: static;
        border-top: none;
        border-radius: 0;
        padding: 0;
        box-shadow:none;
        background: #fff;
        min-width: 100%;

        max-height: 0;
        overflow: hidden;
    
        pointer-events: none;
        opacity: 0;
        transform: translateY(-50px);
        transition: all 0.3s ease;
        
    
        & a {
            display: block;
            padding: 6px 0;
            font-weight: 400;
            font-size: 16px;
            line-height: 130%;
            text-decoration: underline;
            color: var(--temnyy);
            position: relative;
            padding-left: 16px;
            &::after {
                content: "";
                display: block;
                width: 6px;
                height: 6px;
                border-radius: 100%;
                background: var(--akcent);
                position: absolute;
                top: 14px;
                left: 0;
            }
        }
    }
}

@media (max-width: 768px) {
    .main {
        margin-top: 84px;
    }
    .header__menu--active {
        top: 84px;
        height: calc(100% - 84px);
    }

    .header__logo {
        display: block;
        overflow: hidden;
        max-width: 44px;
        max-height: 44px;
        min-width: 40px;
        width: 100%;
    }

}

