
// packages
.packages {
    //overflow: hidden;
    &__card {
        background: rgba(66, 133, 244, 0.05);
    }

    &__name {
        font-weight: 700;
        font-size: 24px;
        line-height: 110%;
        margin-bottom: 8px;
    }

    &__price {
        font-weight: 500;
        font-size: 20px;
    }

    &__inner {
        padding: 24px;
    }

    &__list {
        padding-bottom: 20px;
        border-bottom: 1px solid rgba(60, 50, 50, 0.1);
        margin-bottom: 20px;
        display: flex;
        flex-direction: column;
        gap: 14px;

        & li {
            position: relative;
            padding-left: 20px;
            &::before {
                content: "";
                display: block;
                width: 8px;
                height: 8px;
                border-radius: 100%;
                background: var(--akcent);
                position: absolute;
                top: 9px;
                left: 0;
            }
        }
    }

    &__btn {
        width: 100%;
    }

    &__swiper-pagination {
        margin-top: 14px;
    }

}

.packages-alco {
    &__top {
        padding: 24px;
        background: var(--osnovnoy);
        color: #fff;
    }
}

.packages-narco {
    &__top {
        padding: 20px 0;
        border-top: 1px solid rgba(60, 50, 50, 0.1);
        border-bottom: 1px solid rgba(60, 50, 50, 0.1);
        margin-bottom: 20px;
    }

    &__price {
        color: var(--akcent);
    }

    &__picture img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        max-height: 200px;
    }
    
    &__swiper-paginations {
        margin-top: 14px;
        & .swiper-button-next, & .swiper-button-prev {
            min-width: 36px;
            width: 36px;
            height: 36px;
        }
    }
}

// media packages
@media (max-width: 1024px) {
    .packages {
        &__swiper {
            overflow: visible;
        }
    }
}
@media (max-width: 768px) {
    .packages {
        &__price {
            font-weight: 500;
            font-size: 18px;
        }
        &__inner {
            padding: 20px;
        }
    
        &__list {
            padding-bottom: 16px;
            margin-bottom: 16px;
            gap: 10px;
            & li {
                padding-left: 16px;
                &::before {
                    width: 6px;
                    height: 6px;
                }
            }
        }
    }
    
    .packages-alco {
        &__top {
            padding: 20px;
        }
    }
    
    .packages-narco {
        &__top {
            padding: 16px 0;
            margin-bottom: 16px;
        }
        &__swiper-paginations {
            margin-top: 14px;
            & .swiper-button-next, & .swiper-button-prev {
                display: none;
            }
        }
    }
}