
// prices
.prices {
    &__row {
        display: flex;
        align-items: start;
        gap: 40px;
    }

    &__info {
        flex: 0 0 300px;

        &_block:not(:last-child) {
            padding-bottom: 20px;
            border-bottom: 1px solid rgba(60, 50, 50, 0.1);
            margin-bottom: 20px;
        }
    }

    &__payment {
        &_name {
            font-weight: 700;
            font-size: 20px;
            line-height: 110%;
            color: var(--temnyy);
            margin-bottom: 16px;
        }
        &_list {
            display: flex;
            flex-wrap: wrap;
            gap: 8px;
            & li {
                flex: 1 0 30%;
                border: 1px solid rgba(66, 133, 244, 0.2);
                border-radius: 2px;
                padding: 12px 4px;
                background: rgba(66, 133, 244, 0.1);
                font-size: 16px;
                line-height: 130%;
                color: var(--osnovnoy);
                text-align: center;
            }
        }
    }

    &__warning {
        &_wrap {
            border-left: 4px solid var(--akcent);
            padding: 24px;
            background: #fef8f8;
        }

        &_name {
            font-weight: 700;
            font-size: 20px;
            line-height: 110%;
            color: var(--akcent);
            margin-bottom: 12px;
        }

        &_text {
            font-size: 16px;
            & a {
                text-decoration: underline;
                color: var(--akcent);
                transition: all 0.2s ease-in;
                &:hover {
                    color: rgba(237, 69, 69, 0.6);
                }
            }
        }
        &_phone {
            padding-top: 16px;
            border-top: 1px solid rgba(60, 50, 50, 0.1);
            margin-top: 16px;
            & a {
                font-weight: 700;
                font-size: 20px;
                line-height: 110%;
                color: var(--temnyy);
                display: flex;
                align-items: center;
                gap: 10px;
                transition: all 0.2s ease-in;
                &:hover {
                    color: var(--akcent);
                }
            }
        }
    }

    &__right {
        flex-grow: 1;
    }

    &__table {
        display: block;
        width: 100%;
        margin-bottom: 24px;

        &_thead {
            display: block;
            width: 100%;
            & tr {
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;
                gap: 20px;
                padding: 20px;
                background: var(--osnovnoy);
                color: #fff;
            }
        }

        &_body {
            display: block;
            width: 100%;
        }

        &_item {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 20px;
            padding: 20px;
            &:nth-child(even) {
                background: #f6f9fe;
            }
        }


        &_name a {
            color: var(--temnyy);
            transition: all 0.2s ease-in;
            &:hover {
                text-decoration: underline;
                color: var(--osnovnoy);
            }
        }

        &_btn {
            font-weight: 500;
            text-decoration: underline;
            text-align: right;
            color: var(--osnovnoy);
            transition: all 0.2s ease-in;
            &:hover {
                color: rgba(66, 133, 244, 0.6);
            }
        }
    }

    &__content {
        padding-bottom: 28px;
        border-bottom: 1px solid rgba(60, 50, 50, 0.1);
        margin-bottom: 28px;
    }
}

// media prices
@media (max-width: 900px) {
    .prices {
        &__row {
            flex-direction: column;
            align-items: stretch;
            gap: 20px;
        }
    
        &__info {
            display: contents;
            &_block:not(:last-child) {
                padding-bottom: 0;
                border-bottom: none;
                margin-bottom: 0;
            }
            &_block:not(:first-child) {
                padding-top: 20px;
                border-top: 1px solid rgba(60, 50, 50, 0.1);
            }
        }
    
        &__payment {
            order: 2;
        }
    
        &__warning {
            order: 3;
        }
    
        &__right {
            flex-grow: 1;
        }
    }
}
@media (max-width: 768px) {
    .prices {
        &__payment {
            &_list {
                & li {
                    font-size: 14px;
                }
            }
        }
    
        &__warning {
            &_wrap {
                padding: 20px 18px;
            }
        }
    
        &__table {
            margin-bottom: 16px;
            &_thead {
                & tr {
                    padding: 16px;
                }
            }
            &_item {
                padding: 16px;
            }
        }

        &__content {
            padding-bottom: 20px;
            margin-bottom: 20px;
        }
    }
}
